<template>
  <div>
    <div class="pageMain">
        <van-cell-group class="contentBox">
          <van-list
            v-model="loading"
            :finished="finished"
            :finished-text="list.length>0?'没有更多了':''"
            @load="getSalaryDetailsList"
          >
            <van-cell
              v-for="(item, index) in list"
              :key="index"
              :title="item.employeeName+item.startyear+'年' + item.startmonth + '月工资表'"
              is-link
              :to="'/wagesDetails/' + item.id"
            >
              <template>
                <span v-if="item.showState == '待确认'" class="rightValueY">{{item.showState}}</span>
                <span v-if="item.showState == '待发放'" class="rightValueY">{{item.showState}}</span>
                <span v-if="item.showState == '已拒绝'" class="rightValueY">{{item.showState}}</span>
                <span v-if="item.showState == '已发放'" class="rightValueB">{{item.showState}}</span>
              </template>
            </van-cell>
          </van-list>
        </van-cell-group>
        <van-empty v-if="list.length==0" description="暂无工资信息" />
    </div>
  </div>
</template>

<script>
export default {
  name: "Wages",
  data() {
    return {
      list: [],
      loading: false,
      finished: false,
      pageNum:1,
      pageSize:20
    };
  },
  methods: {
    getSalaryDetailsList() {
      let projectId = JSON.parse(localStorage.getItem("project")).projectid;
      let data = {
        projectId,
        pageNum:this.pageNum,
        pageSize:this.pageSize
      }
      this.$http.jsb.getSalaryDetailsList(data).then((res) => {
        if (res.code == 200) {
          this.pageNum++;
          this.list = this.list.concat(res.data);
          for (var i = 0; i < this.list.length; i++) {
            var item = this.list[i]
            var stateCode = item.stateCode
            var bankStatus = item.bankStatus
            var state = item.state
            var showState = ''
            if (stateCode == 'APPROVED' && state == '0') {
              showState = '待确认'
            }
            if (
              (stateCode == 'APPROVED' && state == '1') ||
              stateCode == 'CONFIRMED' ||
              (stateCode == 'APPROVED' && bankStatus == 'NO')
            ) {
              showState = '待发放'
            }
            if (
              state == '2'
            ) {
              showState = '已拒绝'
            }
            if (stateCode == 'APPROVED' && bankStatus == 'YES') {
              showState = '已发放'
            }
            item.showState = showState
          }
          // 加载状态结束
          this.loading = false;
          // 数据全部加载完成

          if (res.data.length == 0) {
            this.finished = true;
          }
        }
      });
    },
  },
};
</script>

<style lang="less" scoped>
.van-cell__title {
  display: inline-table;
}
</style>